@import '../../colors.scss';

.button {
    position: fixed !important;
    right: 16px;
    bottom: 28px;
    z-index: 5;
    transition: opacity 0.3s;

	i {
        margin-top: 6px;
        border-top: 2px solid $INDIGO;
        border-left: 2px solid $INDIGO;
        height: 10px;
        width: 10px;
        transform: rotate(45deg);
	}

    &.hidden {
        opacity: 0;
        pointer-events: none;
    }
}