@import "../../colors.scss";

.section {
    padding: 68px 0;

    & > * {
        padding-right: 28px;
        padding-left: 28px;
    }
}

.background {
    background-color: $WHITE2;
}

.pattern::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("../../assets/light/images/pattern.png");
    opacity: 0.5;
}